#upload-table-name,
#records-pages-name {
	height: 50px;
}
#app-content>div {
	width: 100%;
	height: 100%;
	padding: 20px;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	background-color: var(--color-main-background)
}
#bridgecontent .icon-file {
	height: 50px;
}
#Upload-table {
	width: 100%;
	border-top: 0;
	border-bottom: 0;
}
#bridgecontent td,
