.sortable-table {
	width: 100%;
	border-collapse: collapse;
}
.sortable-table th,
.sortable-table td {
	padding: 4px;
	border-inline-end: 1px solid;
	border-color: var(--color-border);
}
.sortable-table .sortheader {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}
.sortable-table .sortbuttons {
	display: flex;
	flex-direction: column;
}
.sortable-table .sorttop {
	border-bottom-left-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
}
.sortable-table .sortbottom {
	border-top-left-radius: 0 !important;
	border-top-right-radius: 0 !important;
}
.sortable-table .sortbottom,
.sortable-table .sorttop {
	min-height: 22px !important;
	height: 22px;
}
`, "",{"version":3,"sources":["webpack://./src/components/SortableTable.vue"],"names":[],"mappings":";AA4GA;CACC,WAAW;CACX,yBAAyB;AAC1B;AAEA;;CAEC,YAAY;CACZ,4BAA4B;CAC5B,iCAAiC;AAClC;AAEA;CACC,aAAa;CACb,mBAAmB;CACnB,mBAAmB;CACnB,8BAA8B;AAC/B;AAEA;CACC,aAAa;CACb,sBAAsB;AACvB;AAEA;CACC,uCAAuC;CACvC,wCAAwC;AAEzC;AAEA;CACC,oCAAoC;CACpC,qCAAqC;AACtC;AAEA;;CAEC,2BAA2B;CAC3B,YAAY;AACb","sourcesContent":["<template>\n\t<table class=\"sortable-table\">\n\t\t<thead>\n\t\t\t<tr>\n\t\t\t\t<template v-for=\"(field, index) in fields\">\n\t\t\t\t\t<th v-if=\"fields.at(index).active\" :key=\"'header-fields' + index\">\n\t\t\t\t\t\t<div class=\"sortheader\">\n\t\t\t\t\t\t\t{{ field.name }}\n\t\t\t\t\t\t\t<span class=\"sortbuttons\">\n\t\t\t\t\t\t\t\t<NcButton\n\t\t\t\t\t\t\t\t\tvariant=\"secondary\"\n\t\t\t\t\t\t\t\t\taria-label=\"Ascending Sort\"\n\t\t\t\t\t\t\t\t\tclass=\"sorttop\"\n\t\t\t\t\t\t\t\t\t:disabled=\"sortBy === field.label && sortDir === 'asc'\"\n\t\t\t\t\t\t\t\t\t@click=\"sortByColumn(field.label, 'asc')\">\n\t\t\t\t\t\t\t\t\t<template #icon>\n\t\t\t\t\t\t\t\t\t\t<Triangle :size=\"10\" />\n\t\t\t\t\t\t\t\t\t</template>\n\t\t\t\t\t\t\t\t</NcButton>\n\t\t\t\t\t\t\t\t<NcButton\n\t\t\t\t\t\t\t\t\tvariant=\"secondary\"\n\t\t\t\t\t\t\t\t\taria-label=\"Descending Sort\"\n\t\t\t\t\t\t\t\t\tclass=\"sortbottom\"\n\t\t\t\t\t\t\t\t\t:disabled=\"sortBy === field.label && sortDir === 'desc'\"\n\t\t\t\t\t\t\t\t\t@click=\"sortByColumn(field.label, 'desc')\">\n\t\t\t\t\t\t\t\t\t<template #icon>\n\t\t\t\t\t\t\t\t\t\t<TriangleDown :size=\"10\" />\n\t\t\t\t\t\t\t\t\t</template>\n\t\t\t\t\t\t\t\t</NcButton>\n\t\t\t\t\t\t\t</span>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</th>\n\t\t\t\t</template>\n\t\t\t</tr>\n\t\t</thead>\n\t\t<tbody>\n\t\t\t<tr v-for=\"(row, index) in sortedRows\" :key=\"'rows' + index\">\n\t\t\t\t<template v-for=\"(field, findex) in fields\">\n\t\t\t\t\t<td v-if=\"field.active\" :key=\"'body-fields' + findex\" :class=\"field.extraClass\">\n\t\t\t\t\t\t<a v-if=\"field.type === 'link'\" :href=\"row[field.label]\">\n\t\t\t\t\t\t\t{{ row[field.label] }}\n\t\t\t\t\t\t</a>\n\t\t\t\t\t\t<template v-else>\n\t\t\t\t\t\t\t{{ row[field.label] }}\n\t\t\t\t\t\t</template>\n\t\t\t\t\t</td>\n\t\t\t\t</template>\n\t\t\t</tr>\n\t\t</tbody>\n\t</table>\n</template>\n\n<script>\nimport { NcButton } from '@nextcloud/vue'\nimport Triangle from 'vue-material-design-icons/Triangle.vue'\nimport TriangleDown from 'vue-material-design-icons/TriangleDown.vue'\nexport default {\n\tname: 'SortableTable',\n\tcomponents: {\n\t\tTriangle,\n\t\tTriangleDown,\n\t\tNcButton,\n\t},\n\n\tprops: {\n\t\tfields: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\n\t\trows: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\t},\n\n\tdata() {\n\t\treturn {\n\t\t\tsortBy: 'createdAt',\n\t\t\tsortDir: 'desc',\n\t\t}\n\t},\n\n\tcomputed: {\n\t\tsortedRows() {\n\t\t\tif (!this.sortBy) {\n\t\t\t\treturn this.rows\n\t\t\t}\n\t\t\tconst rowsSorted = this.rows.slice(0).sort((rowA, rowB) => {\n\t\t\t\treturn rowA[this.sortBy] > rowB[this.sortBy]\n\t\t\t})\n\t\t\tif (this.sortDir === 'desc') {\n\t\t\t\trowsSorted.reverse()\n\t\t\t}\n\t\t\treturn rowsSorted\n\t\t},\n\t},\n\n\tmethods: {\n\t\tsortByColumn(columnName, direction) {\n\t\t\tthis.sortBy = columnName\n\t\t\tthis.sortDir = direction\n\t\t},\n\t},\n}\n</script>\n\n<style>\n.sortable-table {\n\twidth: 100%;\n\tborder-collapse: collapse;\n}\n\n.sortable-table th,\n.sortable-table td {\n\tpadding: 4px;\n\tborder-inline-end: 1px solid;\n\tborder-color: var(--color-border);\n}\n\n.sortable-table .sortheader {\n\tdisplay: flex;\n\tflex-direction: row;\n\talign-items: center;\n\tjustify-content: space-between;\n}\n\n.sortable-table .sortbuttons {\n\tdisplay: flex;\n\tflex-direction: column;\n}\n\n.sortable-table .sorttop {\n\tborder-bottom-left-radius: 0 !important;\n\tborder-bottom-right-radius: 0 !important;\n\n}\n\n.sortable-table .sortbottom {\n\tborder-top-left-radius: 0 !important;\n\tborder-top-right-radius: 0 !important;\n}\n\n.sortable-table .sortbottom,\n.sortable-table .sorttop {\n\tmin-height: 22px !important;\n\theight: 22px;\n}\n</style>\n"],"sourceRoot":""}]);
